<script>
import DataSettings from '../../CommonModule/DataSettings'
import BasicSettings from '../../CommonModule/BasicSettings'

export default {
  name: 'LinesAreaChartStyle',
  components: {
    DataSettings,
    BasicSettings
  },
  props: ['activeElement', 'tab'],
  data () {
    return {
      isShowX: false,
      isShowY: false,
      isShowLine: false,
      isShowSet: true,
      isShowLegend: true,
      icon: [
        {
          name: '无',
          value: 'none'
        }, {
          name: '圆形',
          value: 'circle'
        }, {
          name: '矩形',
          value: 'rectangle'
        }, {
          name: '三角形',
          value: 'triangle'
        }, {
          name: '空心圆',
          value: 'emptyCircle'
        }, {
          name: '菱形',
          value: 'diamond'
        }, {
          name: '标注',
          value: 'pin'
        }, {
          name: '箭头',
          value: 'arrow'
        }
      ],

      editableTabsValue: '0'
    }
  },
  computed: {
    editableTabs () {
      let data = this.activeElement.dataLength
      let tabs = new Array(data)
      for (let i = 0; i < data; i++) {
        tabs[i] = {
          title: 'Tab' + (i + 1),
          name: '' + i
        }
      }
      return tabs
    }
  },
  methods: {
    handleClick (tab, event) {
      // console.log(this.editableTabsValue)
    },
    handleTabsEdit (targetName, action) {
      // console.log(targetName, this.editableTabsValue)
      if (action === 'add') {
        let newTabName = this.editableTabs.length + ''
        this.editableTabs.push({
          title: 'Tab' + (this.editableTabs.length + 1),
          name: newTabName
        })
        this.editableTabsValue = newTabName
      }
      if (action === 'remove') {
        let tabs = this.editableTabs
        let activeName = this.editableTabsValue
        if (activeName === targetName) {
          tabs.forEach((tab, index) => {
            if (tab.name === targetName) {
              let nextTab = tabs[index + 1] || tabs[index - 1]
              if (nextTab) {
                activeName = nextTab.name
              }
            }
          })
        }

        this.editableTabsValue = activeName
        this.editableTabs = tabs.filter(tab => tab.name !== targetName)
      }
    },
    addLGColor () {
      let params = {
        property: 'lgArr',
        data: {
          color: '#0000ff',
          offset: 1
        }
      }
      this.$vpd.commit('ADD_COLOR', params)
    },
    delLGColor (index) {
      let params = {
        property: 'lgArr',
        data: {
          index: index
        }
      }
      this.$vpd.commit('DELETE_COLOR', params)
    }
  }
}
</script>
